import {getRequest} from "./api";

export const initMenu = (router, store) => {
    if (store.state.routes.length > 0) {
        return;
    }
    getRequest("/system/config/menu").then(data => {
        console.log(" :", data);  //  
        if (data) {
            let fmtRoutes = formatRoutes(data);
            console.log(" :", fmtRoutes);  //   `component` 
            router.addRoutes(fmtRoutes);
            store.commit('initRoutes', fmtRoutes);
            store.dispatch('connect');
        }
    }).catch(error => {
        console.error(" :", error);
    });
};

export const formatRoutes = (routes) => {
    let fmRoutes = [];
    routes.forEach(router => {
        let {
            path,
            component,
            name,
            meta,
            iconCls,
            children
        } = router;

        if (!component) {
            console.warn(" :", name);
            return;
        }

        console.log("  component:", component);  //   `component`  `Gas`  `Inspection`

        if (children && children instanceof Array) {
            children = formatRoutes(children);
        }

        let fmRouter = {
            path: path,
            name: name,
            iconCls: iconCls,
            meta: meta,
            children: children,
            component(resolve) {
                console.log(" :", component);  //   `Gas.vue`  `Inspection.vue`
                try {
                    if (component.startsWith("Home")) {
                        require(['../views/' + component + '.vue'], resolve);
                    } else if (component.startsWith("Emp")) {
                        require(['../views/emp/' + component + '.vue'], resolve);
                    } else if (component.startsWith("Per")) {
                        require(['../views/per/' + component + '.vue'], resolve);
                    } else if (component.startsWith("Sal")) {
                        require(['../views/sal/' + component + '.vue'], resolve);
                    } else if (component.startsWith("Sta")) {
                        require(['../views/sta/' + component + '.vue'], resolve);
                    } else if (component.startsWith("Sys")) {
                        require(['../views/sys/' + component + '.vue'], resolve);
                    } else if (component.startsWith("Gas") ||
                        component === "Inspection" || component === "Equipment" ||
                        component === "Monitoring" || component === "Documents" ||
                        component === "Materials" || component === "Response" ||
                        component === "Risk" || component === "Warning") {
                        require(['../views/gas/' + component + '.vue'], resolve);
                    } else {
                        console.warn(" :", component);
                    }
                } catch (e) {
                    console.error(" ", e);
                }
            }
        };
        fmRoutes.push(fmRouter);
    });
    return fmRoutes;
};
